<template>
  <div>
    <van-nav-bar title="我的设置" @click-left="Return" class="bgc">
      <van-icon name="arrow-left" slot="left" />
    </van-nav-bar>
    <div class="content">
      <van-collapse v-model="activeNames">
        <!-- 账户与安全 -->
        <van-cell title="账户与安全" />
        <!-- 找回密码 -->
        <van-cell title="找回密码" is-link @click="goto()" />
        <van-cell title="退出登录" @click="query(0)" is-link />

        <!-- 注销账号 -->
        <van-cell title="注销账户" @click="query(1)" is-link />
        <van-dialog
          v-model="show"
          @confirm="destroy()"
          :title="sta==0?'退出登录':'注销账户'"
          show-cancel-button
        >
          <h3>{{sta==0?'是否退出登录':'是否注销账号'}}</h3>
        </van-dialog>
      </van-collapse>
    </div>
  </div>
</template>

<script>
import "../../assets/css/commaint.less";
export default {
  data() {
    return {
      // 激活下拉键
      activeNames: [],
      show: false,
      sta: ""
    };
  },
  methods: {
    goto() {
      this.$router.push({ name: "Finduser" });
    },
    query(v) {
      this.sta = v;
      this.show = true;
    },

    // 注销账号||退出登录
    destroy() {
      let tokenString = localStorage.getItem("__Tk");

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
      let url = this.sta ? "/destroyAccount" : "/logout";

      this.axios({
        method: "POST",
        url,
        params: {
          appkey: this.appkey,
          tokenString
        }
      })
        .then(res => {
          this.$toast.clear();
          if (res.data.code == "G001" || res.data.code == "F001") {
            localStorage.removeItem("__Tk");
            this.$toast(res.data.msg);
          }
        })
        .catch(err => {
          this.$toast.clear();
        });
    },

    // 返回
    Return() {
      this.$router.go(-1);
    },

    // 清除缓存
    clear() {
      this.$toast("成功清除缓存");
    }
  }
};
</script>

<style lang="less" scoped>
.van-dialog {
  h3 {
    text-align: center;
  }
  .van-icon {
    color: #2c1907;
  }
}
</style>